<template>
<div class="liuyan">
  
<el-row>
  <el-col :span="6" :offset="1" :xs="22">
    <h1 style="color:blue" class="hidden-xs-only"><i class="el-icon-edit"></i><i>共有{{length}}条留言</i></h1>
    <br>
    <el-input type="text" placeholder="把你想说的话留下吧" suffix-icon="el-icon-edit" v-model="liuyan" @change="commit" ></el-input>

  </el-col>
  
  <el-col :span="10" :offset="6" >
     <h1 style="color:blue" class="hidden-xs-only"><i class="el-icon-s-data"></i><i>留言列表</i></h1>
     <el-card class="box-card liuyans "  v-for="item in liuyans" :key="item">
      <i>{{item}}</i>
 <a href="" @click.prevent="shan"><i class="el-icon-close"></i></a>
    </el-card>
   </el-col>
</el-row>
</div>
</template>

<script>
export default {
name:'Liuyan',
data(){
  return {
    liuyans:['Practice makes perfect!!!','good luck!','不错不错'],
    liuyan:'',
    length:3
  }
},

methods:{
  shan(){
    
        this.$confirm('此操作将永久删除该留言, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.liuyans.pop()
    this.length--;
          this.$message({
            type: 'success',
            message: '留言删除成功！!'
          });
        }).catch(() => {
          
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
  },

  commit(){
    this.liuyans.push(this.liuyan)
    this.length++;
    this.$message({
          message: '留言发布成功！',
          type: 'success'
        });
  }
}
}
</script>

<style>
.liuyan a{
  float: right;
  color: blue;
  text-decoration: none;
}

.time {
  float: right;
    font-size: 13px;
    color: #999;
  }
.liuyans{
  text-align: left;
  margin: 10px 0;
}

</style>



