<template>
<div class="demo-input-suffix yinyue">
    <el-row type="flex"  justify="space-around">
      <el-col :span="4" :xs='11'>
<audio :src="dizhi" controls="controls" autoplay="autoplay"   ></audio>
<el-col :span="6">
    </el-col>

    </el-col>

    <el-col :span="6" :xs='11'>

      <el-input
    placeholder="请输入歌手、歌曲名"
    prefix-icon="el-icon-search"
    v-model="singer"
    @change="sou">
  </el-input>
    </el-col>
    
</el-row>
  
  <el-row type="flex"  justify="space-around">
  <el-col :span="9" :xs="24">
    <el-card class="box-card">
      <ul>
        <li v-for="item in song" :key="item.id"  @click="bofang(item.id)">{{item.name}}</li>
      </ul>
 
</el-card>

     </el-col>

  <el-col :span="10" class="hidden-xs-only">
    <el-button type="danger"><h2>热门评论</h2></el-button>
    
<el-card class="box-card" v-for="item in pinglun"  :key="item.id" >
  <div class="text item" >
      <i>{{item.content}}</i>
  </div>
</el-card>
<div class="block">
  
</div>
    </el-col>
    
</el-row>

</div>
</template>

<script >
 
export default {
  name:'Yinyue',
  
  data() {
    return {
     song:[],
      singer: '',
      dizhi:'',
      pinglun:[],
      
      
      
    }
  },
  created(){
    
    let that = this;
this.$axios.get("https://autumnfish.cn/search?keywords=邓伦").then(function(response) {
  that.song = response.data.result.songs;

                            
                        }),
                        this.$axios.get("https://autumnfish.cn/comment/hot?type=0&id=" + '1294899662').then(function(response) {
                                that.pinglun = response.data.hotComments;
                            })
      
  },
  methods:{
        sou: function() {
                        let that = this;
                        this.$axios.get("https://autumnfish.cn/search?keywords=" + this.singer).then(function(response) {
                            that.song = response.data.result.songs;
                        })
                        
                    },
    bofang: function(id) {
                        let that = this;
                        
                        this.$axios.get("https://autumnfish.cn/song/url?id=" + id).then(function(response) {
                                that.dizhi = response.data.data[0].url;
                            }),
                            
                            this.$axios.get("https://autumnfish.cn/comment/hot?type=0&id=" + id).then(function(response) {
                                that.pinglun = response.data.hotComments;
                            })
                    }
  }
};
</script>  

<style scoped>
.yinyue .el-card{
  text-align: left;
  
}
.yinyue ul{
  margin: 0;
  padding: 0;
}
.yinyue li{
  list-style: none;
  height: 30px;
  line-height:30px ;
  color: white;
  margin-bottom: 10px;

opacity: 0.8;
background: repeating-linear-gradient( 45deg, #8187ff, #8187ff 5px, #6767ff 5px, #6767ff 25px );
  overflow: hidden;
}
.yinyue li:hover{
  cursor: pointer;
  color: greenyellow;
}
.yinyue .box-card:nth-child(1){
  background: #f9fafc;
  color: black;
}
.yinyue .box-card{
  background-color: #cf1717;
  color: white;
background-image: radial-gradient(#000dff 0.5px, #cf1717 0.5px);
background-size: 10px 10px;
  margin-top: 10px;
}
.yinyue .el-card div:hover{
  transform: scale(1);
  color: white;
}
 .yinyue .el-row {
    margin-top: 20px;
   
  }
  
 .yinyue .el-col {
    border-radius: 4px;
  }
 .yinyue .bg-purple-dark {
    background: #99a9bf;
  }
 .yinyue .bg-purple {
    background: #d3dce6;
  }
 .yinyue .bg-purple-light {
    background: #e5e9f2;
  }
 .yinyue .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
 .yinyue .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }

audio{
  width: 100%;
  height: 100%;
}
</style>


