import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Liuyan from '@/views/Liuyan'
import Game from '@/views/Game'
import Yinyue from '@/views/Yinyue'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
     redirect:'/home'
    },
    {
      path:'/home',
      name:'home',
      component:Home
    },
    {
      path:'/liuyan',
      name:'liuyan',
      component:Liuyan
    },{
      path:'/yinyue',
      name:'yinyue',
      component:Yinyue
    },{
      path:'/game',
      name:'game',
      component:Game
    }
  ]
})



