<template>
  <div class="home">
<el-row type="flex"  justify="space-around">
  <el-col :span="11" :xs="24"><el-card shadow="hover">
      <div><i class="el-icon-s-cooperation"></i>我的技能</div>
      
<el-progress :format="js" :text-inside="true" :stroke-width="26" :percentage="90"></el-progress>
<el-progress  :format="vue" :text-inside="true" :stroke-width="26" :percentage="70" status="success"></el-progress>
<el-progress  :format="git" :text-inside="true" :stroke-width="26" :percentage="50" status="warning"></el-progress>
<el-progress :format="jichu" :text-inside="true" :stroke-width="26" :percentage="50" status="exception"></el-progress>
    </el-card>
    
    <el-row type="flex"  justify="space-around">
  
  <el-col :xs="24"><el-card shadow="hover">
    <div><i class="el-icon-user"></i>联系我</div>
    <br>
       <el-descriptions class="margin-top " title="" :column="3" :size="size" border>
    <template slot="extra">
    </template>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-user"></i>
        姓名
      </template>
      豆明月
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-mobile-phone"></i>
        邮箱
      </template>
      2738903840@qq.com
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-location-outline"></i>
        居住地
      </template>
      西安
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-tickets"></i>
        学历
      </template>
      <p size="small">本科</p>
    </el-descriptions-item>
    <el-descriptions-item>
      
      
    </el-descriptions-item>
  </el-descriptions>
         </el-card></el-col>

  
    
</el-row>

    </el-col>

  <el-col :span="6" class="hidden-xs-only"><el-card shadow="hover">
      <Calendar ></Calendar></el-card>
    </el-col>
</el-row>
   <el-row :gutter="20" class="share">
  <el-col ><el-button type="danger" round><h1>干货分享</h1></el-button></el-col>
</el-row>
<el-row :gutter="20" >
  
  <el-col :span="8" :offset="2" class="hidden-xs-only"><img src="../assets/s.png" alt="" class="share"></el-col>
  <el-col :span="5" style="color:white" class="hidden-xs-only"><div class="grid-content bg-purple">
    <br>
    <h1>css</h1>
    <h1 style="color:blue">特效实现,</h1>
    <h1>和灵感网站</h1>   
    </div></el-col>
  <!-- 轮播图 -->

  <el-col :span="8" :xs='24'><div class="grid-content bg-purple">
    <el-card>
    <div class="block">
    <el-carousel height="200px">
      <el-carousel-item v-for="item in imgs" :key="item.id">
      <a :href="item.link" target="_blank"><img :src="item.url"  alt=""></a>
      </el-carousel-item>
    </el-carousel>
  </div>
  </el-card>
    </div></el-col>
</el-row>
  </div>
</template>

<script>
import 'element-ui/lib/theme-chalk/display.css';

import Calendar from 'vue-calendar-component';
export default {
  name:'Home',
  data () {
      return {
        imgs:[{id:1,url:require('../assets/c.png'),link:'https://www.magicpattern.design/'},
        {id:2,url:require('../assets/b.png'),link:'https://csslayout.io/'},
       {id:3,url:require('../assets/a.png'),link:'https://www.magicpattern.design/'}
              
        ],
        size: ''
      };
    },
methods: {
      js(percentage) {
        return percentage === 90 ? 'HTML、CSS、JavaScript' : `${percentage}%`;
      },
      vue(percentage) {
        return percentage === 70 ? 'Vue、VuRouter、ElementUI' : `${percentage}%`;
      },
        git(percentage) {
        return percentage === 50 ? 'Git、小程序、ES6、express' : `${percentage}%`;
      },
      jichu(percentage) {
        return percentage === 50 ? '数据结构、算法、计网、HTTP' : `${percentage}%`;
      }
    },
    
     components: {
    Calendar
  }
}
</script>

<style>
a{
  color: yellow;
  text-decoration: none;
}


img{
  width: 100%;
  height: 100%;
}
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
.home .el-card{
  color: white;
  background:#96d6a9;
}
  .el-row {
   text-align: center;
   margin-top: 25px;
  }
  .el-col {
    border-radius: 4px;
    
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
 
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .el-progress{
margin-top: 20px;
  }

</style>
    



