<template>
  <div id="app" :class="{'bg1':bg1,'bg2':bg2}">
   <div class="banner">
    
<div class="sign">
 
  <div class="pic">
    <img src="./assets/pic.png" alt="">
  </div>
  <h3>welcome to  豆明月</h3>
  <i>^^一枚热爱前端的小萌新^^</i>
   

</div>
   </div>
    <el-menu
  :default-active="$route.path"
  class="el-menu-demo"
  background-color="#01687b"
  text-color="#fff"
  active-text-color="#ffd04b" router>
  <el-menu-item index="/home"   >关于我</el-menu-item>
  <el-menu-item index="/yinyue" >音乐</el-menu-item>
  <el-menu-item index="/game" class="hidden-xs-only">游戏</el-menu-item>
  <el-menu-item index="/liuyan" >给我留言</el-menu-item>
   </el-menu>
   <div class="deng">
     <i :class="{'el-icon-moon':moon, 'el-icon-sunny':sunny}" @click="huan"></i>
   </div>
    <router-view ></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
      return {
        
        moon:true,
        bg1:true,
        sunny:false,
        bg2:false
      };
    },
    methods: {
     
      
    huan(){
      this.moon=!this.moon;
      this.sunny=!this.sunny;
      this.bg1=!this.bg1;
      this.bg2=!this.bg2;
     

    }
    }
}
</script>
<style >

html,body{
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

.bg1{
background-color: #ffc6b5;
background-size: 20px 20px;
background-image:  repeating-linear-gradient(to right, #444cf7, #444cf7 1px, #ffc6b5 1px, #ffc6b5);}
.bg2{
background-color: #000000;
opacity: 0.7;
background-image:  radial-gradient(#ffffff 0.5px, transparent 0.5px), radial-gradient(#ffffff 0.5px, #000000 0.5px);
background-size: 20px 20px;
background-position: 0 0,10px 10px;
}
.banner{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background: url(./assets/ban.png)  ;
  height: 390px;
}
.banner img{
  width: 100%;
  height: 100%;
}
.sign{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 260px;
  height: 200px;
}
.pic{
  border-radius: 50%;
  overflow: hidden;
width: 120px;
height: 130px;
box-shadow:  12px -12px 23px #ffffff,
             -12px 12px 23px #ffffff;

}
.pic img:hover{
animation: zhuan 1s;

}
@keyframes zhuan {
  0%{
transform: rotate(0);
  }100%{
transform: rotate(360deg);

  }
}
.el-menu{
  display: flex;
}
.el-menu-item{
  flex: 1;
  text-align: center;
}
.deng{
  width: 70px;
  height: 70px;
  font-size: 60px;
  color: black;
  position: absolute;
  top: 310px;
  left: 20px;
}

.el-icon-sunny{
  color: white;
  
}


</style>


